<template>
  <div class="flex main" id="app">
    <champion-div class="data_holder block small" id="champion_data" userid="player" isprimary="true"></champion-div>
    <champion-div class="data_holder block small" id="champion_data" userid="target" isprimary="false"></champion-div>
    <champion-spells
      v-for="spellObj in currentSpells" 
      v-bind:key="spellObj.key"
      v-bind:id="spellObj.key"
      v-bind:spellkey="spellObj.key.toUpperCase().replace('I','Passive ')" 
      v-bind:spell="spellObj.spell"
      v-bind:champion="spellObj.champion" 
      v-bind:spriteurl="spellObj.sprite + spellObj.spell.image.sprite">
    </champion-spells>
  </div>
</template>

<script>
import numeral from 'numeral'
import ChampionDiv from './components/ChampionDiv.vue';
import ChampionSpells from './components/ChampionSpells.vue';
// import './javascript/ui.js';
import {setupVue} from './javascript/league_data.js';
// // import './javascript/league_items.js';
import './javascript/calc.js';

// import './javascript/matchreplace.js';

export default {
  name: 'app',
  components: {
    ChampionDiv,
    ChampionSpells,
  },
  data: function () { return {
    currentSpells: [],
    spellComponents: [],
    championList: {
      'None': {
        name: '  -- None --  ',
        id: ''
      }
    },
    championData: {},
    player: null,
    target: null,
  }},
  created: function() {
    setupVue(this);
    this.$root.$app = this;
  },
  computed: {
    skillpoints_used: function() {
      let sum = 0;
      for (const x in this.spellComponents) {
        sum += this.spellComponents[x].spellrankindex + 1;
      }
      return sum;
    },
    outputStyle: function () {
      const t = this.output.preTotalDmg
      const p1 = (this.output.preMagicDmg / t);
      const p2 = (this.output.prePhysicalDmg / t);
      const p3 = (this.output.trueDmg / t);

      const t2 = this.output.totalDmg
      const p4 = (this.output.magicDmg / t2);
      const p5 = (this.output.physicalDmg / t2);
      const p6 = (this.output.trueDmg / t2);
      return [
        {
          left: '0%',
          width: this.percentf(p1),
        },
        {
          left: this.percentf(p1),
          width: this.percentf(p2),
        },
        {
          left: this.percentf(p1 + p2),
          width: this.percentf(p3),
        },
        {
          left: '0%',
          width: this.percentf(p4),
        },
        {
          left: this.percentf(p4),
          width: this.percentf(p5),
        },
        {
          left: this.percentf(p4 + p5),
          width: this.percentf(p6),
        }
      ]
    },
    output: function () {
      const p = this.player;
      const t = this.target;
      let output = {
        preTotalDmg: 0,
        preMagicDmg: 0,
        prePhysicalDmg: 0,
        totalDmg: 0,
        magicDmg: 0,
        physicalDmg: 0,
        trueDmg: 0,
      };
      if (!p || !t) {
        console.log('Re-calc 4.0 failed, missing player and target.');
        return output;
      }

      for (const spellEff of this.spellComponents) {
        switch (spellEff.damagetype) {
          case "physical":
            output.preTotalDmg += spellEff.dmg_premitigation;
            output.prePhysicalDmg += spellEff.dmg_premitigation;
            output.totalDmg += spellEff.dmg_onhit;
            output.physicalDmg += spellEff.dmg_onhit;
            break;
          case "magic":
            output.preTotalDmg += spellEff.dmg_premitigation;
            output.preMagicDmg += spellEff.dmg_premitigation;
            output.totalDmg += spellEff.dmg_onhit;
            output.magicDmg += spellEff.dmg_onhit;
            break;
          case "true":
            output.preTotalDmg += spellEff.dmg_premitigation;

            output.totalDmg += spellEff.dmg_onhit;
            output.trueDmg += spellEff.dmg_onhit;
            break;
          default:
            break;
        }
      }

      const hp_diff = t.total_hp - output.totalDmg;
      if (hp_diff < 0) {
        output.status = "Dead";
        output.overkill = -hp_diff + " damage overkill";
        output.hpRemaining = "0";
        output.hpRemainingPercent = "0%";
      } else if (hp_diff > 1) {
        output.status = "Alive";
        output.overkill = "N/A";
        output.hpRemaining = hp_diff;
        output.hpRemainingPercent = hp_diff / t.total_hp;
      } else {
        output.status = "Dead (Maybe)";
        output.overkill = "N/A";
        output.hpRemaining = hp_diff;
        output.hpRemainingPercent = hp_diff / t.total_hp;
      }
      return output;
    }
  },
  methods: {
    rnd: function (n, digits) {
      if (n === undefined || isNaN(n))
        n = 0;
      if (digits === undefined)
        digits = 0;
      return +((n).toFixed(digits))
    },
    percentf: function (value) {
      return numeral(value).format('0.00%')
    }
  }
}
</script>

